import React from 'react';
import { Route, Switch, Redirect } from 'react-router';
import Home from '../components/home';
import MentorsRoom from '../components/mentors-room';

const routes = (
  <Switch>
    <Route exact path="/mentor-dashboard/" component={Home} />
    <Route path="/mentor-dashboard/mentorsroom" component={MentorsRoom} />
    <Route exact path="" render={() => (
      <Redirect to="/mentor-dashboard/"/>
    )} />
  </Switch>
)

export default routes