import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import { push } from 'connected-react-router';
import firebase from 'firebase';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import './mentor-room.css';

class Details extends Component {
  constructor(props) {
    super(props);
    this.mentor = '';
    this.table = {
      studentsname: [],
      taskname: []
    }
  }

  createHeader() {
    this.table.studentsname.push(<td key='emptyline'></td>);
    this.mentor.students.forEach((student, id) => {
      const tableheader = <td key={id}><a href={student.link} target="_blank" rel="noopener noreferrer">{student.name}</a></td>
      this.table.studentsname.push(tableheader);
      if (id === 0) {
        student.tasks.forEach((task, id) => {
          this.fillTasks(task, id);
        })
      }
    });
    
  }

  fillTasks(task, id) {
    const taskfield = [];
    this.mentor.students.forEach((student, index) => {
      const chooseclass = this.checkTask(student.tasks[id].status);
      taskfield.push(<td key={index} className={chooseclass}></td>);
    })
    this.table.taskname.push(<tr key={id}><td><a href={task.link} target='_blank' rel="noopener noreferrer">{task.task}</a></td>{taskfield}</tr>);
  }

  checkTask(status) {
    switch(status) {
      case 'Checked':
        return 'not-performed';
      case 'Checking':
        return 'need-to-check';
      case 'In Progress':
        return 'in-process';
      case 'ToDo':
        return 'todo-status';
      case 0: 
        return 'not-performed';
      default: return 'checked-by-mentor';
    }
  }

  onClick() {
    window.localStorage.setItem('mentor', undefined);
    firebase.auth().signOut();
    this.props.push('/mentor-dashboard/');
  }

  render() {
    if(!this.props.mentor){
      const metorsdata = window.localStorage.getItem('mentor');
      this.mentor = JSON.parse(metorsdata);
    } else {
      this.mentor = this.props.mentor;
    }
    this.table.studentsname = [];
    this.table.taskname = [];
    this.createHeader();
    return (
      <div className='mentors-room-container flex-container__mentorsroom'>
        <div className='mentors-room__button-container flex-container__mentorsroom'>
          <button className='mentors-room__button flex-container__mentorsroom' onClick={this.onClick.bind(this)}>
            <FontAwesomeIcon icon='sign-out-alt' style={{height: '40', width: '40'}}/>
            logout
          </button>
        </div>
        <h2  className='mentors-room__header'> 
          Mentor: {this.mentor.gitaccount.replace(/https:|github.com|[^a-zA-Z]/g, '').toLowerCase()} 
        </h2>
        <table className='mentors-room__table'>
          <thead className='mentors-room__table-header'>
            <tr>{this.table.studentsname}</tr>
          </thead>
          <tbody className='mentors-room__table-body'>
            {this.table.taskname}
          </tbody>
        </table>
      </div>
    )
  }
}

function  mapStateToProps(state) {
  return {
    mentor: state.active
  };
}

function matchDispatchToProps (dipatch) {
  return bindActionCreators({ push }, dipatch)
}

export default connect(mapStateToProps, matchDispatchToProps)(Details);

