import React from "react";
import { connect } from "react-redux";
import { fetchMentors } from '../actions/get-mentors-action';
import Input from './Input';

import './home.css';

class HomeContainer extends React.Component {
  componentDidMount() {
    this.props.dispatch(fetchMentors());
  }

  render() {
    const { error, loading } = this.props;

    if (error) {
      return <div>Error! {error.message}</div>;
    }

    if (loading) {
      return <div className='loading-container'>
        <img alt='Loading...' src='https://res.cloudinary.com/p-d/image/upload/v1550868069/cod-jam-culture/spinner1.gif'/>
      </div>
    }

    return (
      <Input />
    );
  }
}

const mapStateToProps = state => ({
  loading: state.mentors.loading,
  error: state.mentors.error
});

export default connect(mapStateToProps)(HomeContainer);