import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {select} from '../actions/index';
import { push } from 'connected-react-router';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import firebase from 'firebase';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';

import './input.css';

class Input extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      list: [],
    };
    this.activementor = window.localStorage.getItem('mentor');
    if(this.activementor !== 'undefined') {
      this.props.select(JSON.parse(this.activementor));
      this.props.push('/mentor-dashboard/mentorsroom ');
    }
    this.mentors = this.props.mentors.items;
  }

  uiConfig = {
    signInFlow: 'popup',
    signInOptions: [
      firebase.auth.GithubAuthProvider.PROVIDER_ID
    ],
    callbacks: {
      signInSuccess: () => false,
    },
  }

  componentDidMount() {
    firebase.auth().onAuthStateChanged((user) => {
      this.getMentorLink(user);
    })
  }

  getMentorLink(user) {
    if(user) {
      fetch(`https://api.github.com/user/${user.providerData[0].uid}`)
        .then(res => res.json())
        .then(data => {
          this.redirectToMentorRoom(data.html_url)
        })
    }
  }

  handleChange(event) {
    const list = [];
      if (event.target.value.length !== 0) {
        this.filtredmentors = this.mentors.filter((mentor) => 
        mentor.gitaccount.replace('https://github.com/', '')
          .substr(0, event.target.value.length).toLowerCase() === event.target.value.toLowerCase()
      )
      this.filtredmentors.forEach((mentor, id) => {
        const div = <div key={id} onClick={this.setValueInInput.bind(this)}>{mentor.gitaccount.replace(/https:|github.com|\u002F/g, '').toLowerCase()}</div>
        list.push(div);
      });
      this.setState({list: list});
    } else {
      this.setState({list: []});
    }
    this.setState({value: event.target.value});
  }

  setValueInInput(event) {
    this.setState({value: event.target.innerText})
    this.setState({list: []})
  }

  redirectToMentorRoom(event) {
    this.mentors.forEach((mentor) => {
      let auth = null;
      const correctmentor = mentor.gitaccount.replace(/https:|github.com|\u002F/g, '').toLowerCase();
      if (event) {
        auth = event.replace(/https:|github.com|\u002F/g, '').toLowerCase();
      }
      if (correctmentor === this.state.value || correctmentor === auth) {
        window.localStorage.setItem('mentor', JSON.stringify(mentor));
        this.props.select(mentor);
        this.props.push('/mentor-dashboard/mentorsroom');
      }
    });
  }

  onClick(event) {
    this.redirectToMentorRoom();
    event.preventDefault();
  }

  render() {
    return (
      <div className='header-container flex-container' style={{flexDirection:  "column"}}>
        <div className='flex-container'>
          <h1>Mentors Dashboard</h1>
        </div>
        <div className='header-container flex-container'>
          <form onSubmit= {this.handle} className='header-container__form flex-container'> 
            <div ref="autocomplete" className='header-container__autocomplete'>
              <div className='header-container__autocomplete-inputcontainer flex-container'>
                <FontAwesomeIcon icon='search' className='header-container__autocomplete-icon'/>
                <input className='header-container__autocomplete-input' placeholder="enter gitname" value={this.state.value} onChange={this.handleChange.bind(this)} />
              </div>
              <div className='header-container__autocomplete-list'>
                {this.state.list}
              </div>
            </div>
            <button onClick={this.onClick.bind(this)} className='header-container__form-button'>
              Search 
            </button>
          </form>
          or
          <div>
            <StyledFirebaseAuth className='header-container__github' 
              uiConfig={this.uiConfig}
              firebaseAuth={firebase.auth()}
            >
              <FontAwesomeIcon icon={['fab', 'github']} className='header-container__github-icon'/>
            </StyledFirebaseAuth>
          </div>
        </div>
      </div>
    )
  }
}

function  mapStateToProps(state) {
  return {
    mentors: state.mentors,
    link: state.link,
  };
}

function matchDispatchToProps (dispatch) {
  return bindActionCreators({select: select, push }, dispatch)
}

export default connect(mapStateToProps,matchDispatchToProps)(Input);