import {
  FETCH_MENTORS_BEGIN,
  FETCH_MENTORS_SUCCESS,
  FETCH_MENTORS_FAILURE
} from '../actions/get-mentors-action';

const initialState = {
  items: [],
  loading: false,
  error: null
};

export default function (state = initialState, action) {
  switch(action.type) {
    case FETCH_MENTORS_BEGIN:
      return {
        ...state,
        loading: true,
        error: null
      };

    case FETCH_MENTORS_SUCCESS:
      return {
        ...state,
        loading: false,
        items: action.payload.mentors
      };

    case FETCH_MENTORS_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.payload.error,
        items: []
      };

    default:
      return state;
  }
}